<template>
    <div class="content-wrapper">
        <div class="content-header row">
            <div class="content-header-left col-md-9 col-12 mb-2">
                <div class="row breadcrumbs-top">
                    <div class="col-12">
                        <h2 class="content-header-title float-left mb-0">
                            Dashboard
                        </h2>
                        <div class="breadcrumb-wrapper">
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <!-- <NuxtLink to="/dashboard">Dashboard </NuxtLink> -->
                                </li>
                                <li class="breadcrumb-item active">
                                    Dashboard
                                </li>
                            </ol>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="content-body">
            <div class="container px-0">
                <div class="justify-content-center">
                    <div class="card">

                        <div class="card-body">
                            <h5 class="mb-3 text-center">SALES ORDERS, PURCHASES AND EXPENSES CHART FOR THE CURRENT YEAR</h5>
                            <Linegraph
                                :chartData="chData"
                                :height="150"
                                :chart-options="boptions"
                            ></Linegraph>
                        </div>
                    </div>

                    <div class="row mx-0">
                        <div class="col-6 pl-0">
                            <div class="card">
                                <div class="card-body">
                            <h5 class="mb-3 text-center">SALES ORDERS CURRENT WEEK</h5>

                                    <Bargraph
                                        :chartData="bhData"
                                        :height="200"
                                        :chart-options="options"
                                        :css-classes="'bar-chart-ex chartjs chartjs-render-monitor'"
                                    ></Bargraph>
                                </div>
                            </div>
                        </div>
                        <div class="col-6 pr-0">
                            <div class="card">
                                <div class="card-body">
                            <h5 class="mb-3 text-center">TOP SELLING MENUES FOR THE CURRENT WEEK</h5>

                                    <Doughnut
                                        :chartData="dhData"
                                        :height="313"
                                        :chart-options="dnoptions"
                                    ></Doughnut>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MainLayout from "../Shared/Layouts/Vertical.vue";
import Linegraph from "../Shared/Graphs/LineChart.vue";
import Bargraph from "../Shared/Graphs/BarChart.vue";
import Doughnut from "../Shared/Graphs/Doghnut.vue";
export default {
    layout: MainLayout,
    components: {
        Linegraph,
        Bargraph,
        Doughnut,
    },
    data: () => ({
        chData: {},
        bhData: {},
        dhData: {},
        options: {},
        dnoptions: {},
        boptions: {},
        loading: false,
    }),

    methods: {
        async loadStatistics() {
            this.loading = true;
            var response = await axios
                .get("/home/stats")
                .then((response) => {
                    return response.data;
                })
                .catch((error) => {
                    console.log(error);
                    this.loading = false;
                });
            this.loading = false;

            //sort out labels first
            var sales_final_amounts = [];
            var purchase_final_labels = [];
            var expense_final_labels = [];

            //sort out data next
            var sales_final_labels = [];
            var purchase_final_amounts = [];
            var expense_final_amounts = [];

            //generate and consolidate labels
            for (var i in response.sales_orders) {
                sales_final_labels.push(response.sales_orders[i].label);
            }

            for (var i in response.purchases) {
                purchase_final_labels.push(response.purchases[i].label);
            }

            for (var i in response.expenses) {
                expense_final_labels.push(response.expenses[i].label);
            }

            var dynam_labels = sales_final_labels.concat(
                purchase_final_labels.filter(function (item) {
                    return sales_final_labels.indexOf(item) < 0;
                })
            );

            var final_dynam_labels = dynam_labels
                .concat(
                    expense_final_labels.filter(function (item) {
                        return dynam_labels.indexOf(item) < 0;
                    })
                )
                .sort(this.sortByMonthYear);

            //map data based on labels generated
            final_dynam_labels.forEach((element) => {
                var temp_sales = response.sales_orders.find(
                    (item) => item.label == element
                );
                var temp_purchases = response.purchases.find(
                    (item) => item.label == element
                );
                var temp_expenses = response.expenses.find(
                    (item) => item.label == element
                );

                if (!_.isEmpty(temp_sales)) {
                    sales_final_amounts.push(temp_sales.amount);
                } else {
                    sales_final_amounts.push(0);
                }

                if (!_.isEmpty(temp_purchases)) {
                    purchase_final_amounts.push(temp_purchases.amount);
                } else {
                    purchase_final_amounts.push(0);
                }

                if (!_.isEmpty(temp_expenses)) {
                    expense_final_amounts.push(temp_expenses.amount);
                } else {
                    expense_final_amounts.push(0);
                }
            });

            this.boptions = {
                plugins: {
                    legend: {
                        labels: {
                            usePointStyle: true,
                        },
                    },
                },
            };

            var barchart_final_amounts = [];
            var barchart_final_labels = [];

            for (var i in response.sales_orders_this_week) {
                barchart_final_amounts.push(
                    response.sales_orders_this_week[i].amount
                );
                barchart_final_labels.push(
                    response.sales_orders_this_week[i].label
                );
            }

            this.options = {
                elements: {
                    rectangle: {
                        borderWidth: 2,
                        borderSkipped: "bottom",
                    },
                },
                responsive: true,
                maintainAspectRatio: false,
                responsiveAnimationDuration: 500,
                legend: {
                    display: false,
                },
                tooltips: {
                    // Updated default tooltip UI
                    shadowOffsetX: 1,
                    shadowOffsetY: 1,
                    shadowBlur: 8,
                    shadowColor: "rgba(0, 0, 0, 0.25)",
                    backgroundColor: "#ffffff",
                    titleFontColor: "black",
                    bodyFontColor: "black",
                },
                scales: {
                    xAxes: [
                        {
                            display: true,
                            gridLines: {
                                display: true,
                                color: "rgba(200, 200, 200, 0.2)",
                                zeroLineColor: "rgba(200, 200, 200, 0.2)",
                            },
                            scaleLabel: {
                                display: false,
                            },
                            ticks: {
                                fontColor: "#b4b7bd",
                            },
                        },
                    ],
                    yAxes: [
                        {
                            display: true,
                            gridLines: {
                                color: "rgba(200, 200, 200, 0.2)",
                                zeroLineColor: "rgba(200, 200, 200, 0.2)",
                            },
                            ticks: {
                                stepSize: 100,
                                min: 0,
                                max: 400,
                                fontColor: "#b4b7bd",
                            },
                        },
                    ],
                },
            };
            var data = {
                labels: final_dynam_labels,
                datasets: [
                    {
                        data: sales_final_amounts,
                        label: "Sales Orders",
                        borderColor: "#ff4961",
                        lineTension: 0.5,
                        pointStyle: "circle",
                        backgroundColor: "#ff4961",
                        fill: false,
                        pointRadius: 1,
                        pointHoverRadius: 5,
                        pointHoverBorderWidth: 5,
                        pointBorderColor: "transparent",
                        pointHoverBorderColor: "#ffffff",
                        pointHoverBackgroundColor: "#ff4961",
                        pointShadowOffsetX: 1,
                        pointShadowOffsetY: 1,
                        pointShadowBlur: 5,
                        pointShadowColor: "rgba(0, 0, 0, 0.25)",
                    },
                    {
                        data: purchase_final_amounts,
                        label: "Purchases",
                        borderColor: "#666ee8",
                        lineTension: 0.5,
                        pointStyle: "circle",
                        backgroundColor: "#666ee8",
                        fill: false,
                        pointRadius: 1,
                        pointHoverRadius: 5,
                        pointHoverBorderWidth: 5,
                        pointBorderColor: "transparent",
                        pointHoverBorderColor: "#ffffff",
                        pointHoverBackgroundColor: "#666ee8",
                        pointShadowOffsetX: 1,
                        pointShadowOffsetY: 1,
                        pointShadowBlur: 5,
                        pointShadowColor: "rgba(0, 0, 0, 0.25)",
                    },
                    {
                        data: expense_final_amounts,
                        label: "Expenses",
                        borderColor: "#ffe802",
                        lineTension: 0.5,
                        pointStyle: "circle",
                        backgroundColor: "#ffe802",
                        fill: false,
                        pointRadius: 1,
                        pointHoverRadius: 5,
                        pointHoverBorderWidth: 5,
                        pointBorderColor: "transparent",
                        pointHoverBorderColor: "#ffffff",
                        pointHoverBackgroundColor: "#ffe802",
                        pointShadowOffsetX: 1,
                        pointShadowOffsetY: 1,
                        pointShadowBlur: 5,
                        pointShadowColor: "rgba(0, 0, 0, 0.25)",
                    },
                ],
            };

            this.chData = data;

            this.bhData = {
                labels: barchart_final_labels,
                datasets: [
                    {
                        data: barchart_final_amounts,
                        backgroundColor: "#28dac6",
                        borderColor: "transparent",
                        borderRadius: 15,
                        barPercentage: 0.5,
                        label: "Sales Made This Week",
                    },
                ],
            };

            var doghnut_final_amounts = [];
            var doghnut_final_labels = [];

            for (var i in response.top_menus) {
                doghnut_final_amounts.push(response.top_menus[i].total);
                doghnut_final_labels.push(response.top_menus[i].name);
            }
            this.dhData = {
                labels: doghnut_final_labels,
                datasets: [
                    {
                        label: "Top 3 Menus Sold This Week",
                        data: doghnut_final_amounts,
                        backgroundColor: ["#28dac6", "#FDAC34", "#7367f0"],
                        borderWidth: 0,
                        pointStyle: "rectRounded",
                    },
                ],
            };

            this.dnoptions = {
                responsive: true,
                maintainAspectRatio: false,
                responsiveAnimationDuration: 500,
                cutoutPercentage: 60,
                legend: { display: false },
                tooltips: {
                    callbacks: {
                        label: function (tooltipItem, data) {
                            console.log(data);

                            var label =
                                    data.datasets[0].labels[
                                        tooltipItem.index
                                    ] || "",
                                value =
                                    data.datasets[0].data[tooltipItem.index];
                            var output = " " + label + " : " + value + " %";
                            return output;
                        },
                    },
                    // Updated default tooltip UI
                    shadowOffsetX: 1,
                    shadowOffsetY: 1,
                    shadowBlur: 8,
                    shadowColor: "rgba(0, 0, 0, 0.25)",
                    backgroundColor: "white",
                    titleFontColor: "black",
                    bodyFontColor: "black",
                },
            };

            // this.products = response.products;
            // this.topdealers = response.sales_orders;
            // this.latestorders = response.latest_orders;

            // this.boptions = {
            //     plugins: {
            //         legend: {
            //             labels: {
            //                 usePointStyle: true
            //             }
            //         }
            //     }
            // };

            // var data = {
            //     labels: final_labels,
            //     datasets: [
            //         {
            //             data: final_amounts,
            //             label: "Order Sales",
            //             borderColor: "#ff4961",
            //             lineTension: 0.5,
            //             pointStyle: "circle",
            //             backgroundColor: "rgba(255, 73, 97, 0.5)",
            //             fill: true
            //         }
            //     ]
            // };

            // this.chData = data;
        },

        sortByMonthYear(a, b) {
            var as = a.split(" "),
                bs = b.split(" "),
                ad = new Date(as[0] + " 1," + as[1]),
                bd = new Date(bs[0] + " 1," + bs[1]);
            return ad.getTime() - bd.getTime();
        },
    },

    mounted() {
        this.loadStatistics();
    },
};
</script>
