<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Edit Purchase
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Supplier</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        id="bankSelect"
                                        v-model="supplier_id"
                                    >
                                        <option value="">
                                            Select Supplier
                                        </option>
                                        <option
                                            v-for="(
                                                supplier, index
                                            ) in suppliers"
                                            :value="supplier.id"
                                            :key="index"
                                        >
                                            {{ supplier.name }}
                                        </option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label> Purchase Date</label>
                                    <input
                                        type="date"
                                        class="form-control"
                                        v-model="purchase_date"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Notes</label>
                                    <textarea
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="notes"
                                    ></textarea>
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Payment Status</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        required
                                        v-model="payment_status"
                                    >
                                        <option value="">Select Mode</option>
                                        <option value="1">Paid</option>
                                        <option value="0">Not Paid</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group" v-if="status == false">
                                    <label>Attach File</label>
                                    <input
                                        type="file"
                                        class="form-control"
                                        @input="file = $event.target.files[0]"
                                        required
                                    />
                                </div>
                                <div
                                    v-else
                                    class="card p-2"
                                    style="width: min-content"
                                >
                                    <a
                                        :href="placeholder"
                                        class="rounded-3 shadow-4"
                                        style="width: 100px"
                                        >{{ placeholder }}</a
                                    >
                                    <a
                                        @click="remove"
                                        href="#"
                                        class="btn btn-sm text-danger"
                                        ><feather type="trash-2"></feather
                                    ></a>
                                </div>
                            </div>

                            <div class="col-12">
                                <InventoryDropdown
                                    dynamic_id="select2_edit"
                                ></InventoryDropdown>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";
import InventoryDropdown from "../../components/Purchases/InventoryDropdown.vue";

export default {
    layout: "Vertical",
    data: () => ({
        supplier_id: "",
        purchase_date: "",
        notes: "",
        payment_status: "",
        image: null,
        Id: null,
        file: null,
        status: false,
        placeholder: "",
    }),
    components: {
        InventoryDropdown,
    },
    props: ["suppliers"],

    computed: {
        editdata() {
            return this.$store.state.editData;
        },
        items() {
            return this.$store.state.items;
        },
    },
    methods: {
        clearData() {
            this.supplier_id = "";
            this.purchase_date = "";
            this.notes = "";
            this.payment_status = "";
            this.file = null;
            this.status = false;
            this.$store.commit("clearItemsData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                supplier_id: this.supplier_id,
                purchase_date: this.purchase_date,
                notes: this.notes,
                items: this.items,
                file: this.file,
                payment_status: this.payment_status,
                _method: "patch",
            });

            submitdata.post(route("purchases.update", { purchase: this.Id }));
            $("#editmodal").modal("toggle");
            this.clearData();
        },
        remove() {
            // this.status = false;
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.supplier_id = value.supplier_id;
                this.purchase_date = value.purchase_date;
                this.notes = value.notes;
                this.payment_status = value.payment_status;
                this.status = value.filestatus;
                this.placeholder = value.file;
                this.Id = value.id;
            }
        },
    },
};
</script>
