<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import ProductionCreateModal from "../../components/Production/ProductionCreateModal.vue";
import ProductionSearch from "../../components/Production/ProductionSearch.vue";
import ProductionEditModal from "../../components/Production/ProductionEditModal.vue";
import ViewModal from "../../components/Production/ViewModal.vue";
export default {
    layout: MainLayout,
    components: {
        ProductionCreateModal,
        ProductionSearch,
        ProductionEditModal,
        ViewModal,
    },
    props: ["production"],
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },

    methods: {
        getFun() {
            this.$inertia.get(route("production.index"));
        },
        editFun(data) {
            this.$store.commit("editDataState", data);
        },
        toggleSearchForm() {
            this.$store.commit("toggleSearch");
        },
        deleteFun(id) {
            var getconfirmation = confirm(
                "Are you sure You Want to Delete This Record?"
            );
            if (getconfirmation == true) {
                this.$inertia.delete(route("production.destroy", id));
            }
        },
    },
};
</script>
