<template>
    <div
        class="modal fade text-left"
        id="large"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">New Expense</h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="saveFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Category</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        id="bankSelect"
                                        v-model="category_id"
                                    >
                                        <option value="">
                                            Select Category
                                        </option>
                                        <option
                                            v-for="(
                                                category, index
                                            ) in categories"
                                            :value="category.id"
                                            :key="index"
                                        >
                                            {{ category.name }}
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label> Expense Date</label>
                                    <input
                                        type="date"
                                        class="form-control"
                                        v-model="expense_date"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label> Expense For</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="expensefor"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Notes</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="notes"
                                        v-model="notes"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Amount</label>
                                    <input
                                        type="number"
                                        class="form-control"
                                        v-model="amount"
                                        required
                                    />
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label>Attach File</label>
                                    <input
                                        type="file"
                                        class="form-control"
                                        @input="file = $event.target.files[0]"
                                        required
                                    />
                                </div>
                            </div>
                            <!-- <progress
                                v-if="form.progress"
                                :value="form.progress.percentage"
                                max="100"
                            >
                                {{ submitdata.progress.percentage }}%
                            </progress> -->
                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    data: () => ({
        category_id: "",
        expensefor: "",
        expense_date: "",
        notes: "",
        amount: "",
        file: null,
    }),
    props: ["categories"],
    methods: {
        saveFun() {
            var submitdata = Inertia.form({
                category_id: this.category_id,
                expensefor: this.expensefor,
                expense_date: this.expense_date,
                notes: this.notes,
                amount: this.amount,
                file: this.file,
            });
            submitdata.post(route("expenses.store"));
            $("#large").modal("toggle");
            this.clearData();
        },
        clearData() {
            this.category_id = "";
            this.expensefor = "";
            this.expense_date="";
            this.notes = "";
            this.amount = "";
            this.file = null;
        },
    },
};
</script>
