                <div class="col-12">
                    <div class="card">
                        <div class="card-header py-1">
                            <h4 class="card-title">Purchases</h4>

                            <div class="float-right">
                                <button
                                    type="button"
                                    class="btn btn-outline-primary float-right btn-sm"
                                    data-toggle="modal"
                                    data-target="#large"
                                >
                                    <feather type="plus"></feather>
                                </button>

                                <button
                                    class="btn btn-outline-primary float-right mr-1 btn-sm"
                                    @click="getFun"
                                >
                                    <feather type="refresh-ccw"></feather>
                                </button>

                                <button
                                    class="btn btn-outline-primary float-right mr-1 btn-sm"
                                    @click="toggleSearchForm"
                                >
                                    <feather type="search"></feather>
                                </button>
                            </div>
                        </div>

                        <div v-show="displaySearch">
                            <PurchaseSearch
                                :suppliers="suppliers"
                            ></PurchaseSearch>
                        </div>

                        <div class="table-responsive table-hover table-striped">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>File</th>
                                        <th>PurchaseNo</th>
                                        <th>Supplier</th>
                                        <th style="min-width:150px;">Purchase Date</th>
                                        <th>Notes</th>
                                        <th>Amount</th>
                                        <th>Payment Status</th>
                                        <th>Status</th>
                                        <th>CreatedBy</th>
                                        <th>View Items</th>
