                                    </tr>
                                </thead>

                                <tbody>
                                    <tr
                                        v-for="(item, index) in purchases.data"
                                        :key="index"
                                    >
                                        <td>{{ index + 1 }}</td>
                                        <td>
                                            <a
                                                :href="item.file"
                                                target="_blank"
