                                        <td>
                                            <button
                                                type="button"
                                                class="btn btn-sm btn-primary"
                                                data-toggle="modal"
                                                data-target="#viewmodal"
                                                @click="editFun(item)"
                                            >
                                                <feather type="eye"></feather>
                                            </button>
                                        </td>
                                        <td>
                                            <div class="dropdown">
                                                <button
                                                    type="button"
                                                    class="btn btn-sm dropdown-toggle hide-arrow"
                                                    data-toggle="dropdown"
                                                >
                                                    <feather
                                                        type="more-vertical"
                                                    ></feather>
                                                </button>
                                                <div class="dropdown-menu">
                                                    <a
                                                        class="dropdown-item bg-light-info"
                                                        href="javascript:void(0);"
                                                        @click="updateStatus(item.id)"
                                                    >
                                                        <feather
                                                            type="arrow-right-circle"
                                                            class="mr-50"
                                                        ></feather>

                                                        <span>Status</span>
                                                    </a>
                                                    <a
                                                        class="dropdown-item bg-light-warning"
                                                        href="javascript:void(0);"
                                                        data-toggle="modal"
                                                        data-target="#editmodal"
                                                        @click="editFun(item)"
                                                    >
                                                        <feather
                                                            type="edit-2"
                                                            class="mr-50"
                                                        ></feather>

                                                        <span>Edit</span>
                                                    </a>
                                                    <a
                                                        class="dropdown-item bg-light-danger"
                                                        href="javascript:void(0);"
                                                        @click="
                                                            deleteFun(item.id)
                                                        "
                                                    >
                                                        <feather
                                                            type="trash"
                                                            class="mr-50"
                                                        ></feather>

                                                        <span>Delete</span>
                                                    </a>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                            <div class="mx-2 mt-3">
                                <pagination :data="purchases" :limit="1">
                                    <span slot="prev-nav"
                                        ><feather type="arrow-left"></feather
                                    ></span>
                                    <span slot="next-nav"
                                        ><feather type="arrow-right"></feather
                                    ></span>
                                </pagination>

                                <!-- <pagination class="mt-6" :links="users.links" /> -->
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <PurchaseCreateModal :suppliers="suppliers"></PurchaseCreateModal>
        <PurchaseEditModal :suppliers="suppliers"></PurchaseEditModal>
        <ViewModal></ViewModal>
    </div>
</template>

<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import PurchaseCreateModal from "../../components/Purchases/PurchaseCreateModal.vue";
import PurchaseSearch from "../../components/Purchases/PurchaseSearch.vue";
import PurchaseEditModal from "../../components/Purchases/PurchaseEditModal.vue";
import ViewModal from "../../components/Purchases/ViewModal.vue";
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: MainLayout,
    components: {
        PurchaseCreateModal,
        PurchaseSearch,
        PurchaseEditModal,
        ViewModal,
    },
    props: ["purchases", "suppliers"],
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },

    methods: {
        getFun() {
            this.$inertia.get(route("purchases.index"));
        },
        editFun(data) {
            this.$store.commit("editDataState", data);
        },
        toggleSearchForm() {
            this.$store.commit("toggleSearch");
        },
        deleteFun(id) {
            var getconfirmation = confirm(
                "Are you sure You Want to Delete This Record?"
            );
            if (getconfirmation == true) {
                this.$inertia.delete(route("purchases.destroy", id));
            }
        },
        updateStatus(id) {
            var submitdata = Inertia.form({
                id: id
            });
            var getconfirmation = confirm(
                "Are you sure You Want to Update Purchase Status to complete?"
            );
            if (getconfirmation == true) {
                submitdata.post(route("purchases.updateStatus"));
            }
        },
    },
};
</script>
