<template>
    <div
        class="modal fade text-left"
        id="large"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        New Purchase
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="saveFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Supplier</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        id="bankSelect"
                                        v-model="supplier_id"
                                    >
                                        <option value="">
                                            Select Supplier
                                        </option>
                                        <option
                                            v-for="(
                                                supplier, index
                                            ) in suppliers"
                                            :value="supplier.id"
                                            :key="index"
                                        >
                                            {{ supplier.name }}
                                        </option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label> Purchase Date</label>
                                    <input
                                        type="date"
                                        class="form-control"
                                        v-model="purchase_date"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Notes</label>
                                    <textarea
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="notes"
                                    ></textarea>
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Payment Status</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        required
                                        v-model="payment_status"
                                    >
                                        <option value="">Select Mode</option>
                                        <option value="1">Paid</option>
                                        <option value="0">Not Paid</option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label>Attach File</label>
                                    <input
                                        type="file"
                                        class="form-control"
                                        @input="file = $event.target.files[0]"
                                        
                                    />
                                </div>
                            </div>

                            <div class="col-12">
                                <InventoryDropdown
                                    dynamic_id="select2_create"
                                ></InventoryDropdown>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";
import InventoryDropdown from "../../components/Purchases/InventoryDropdown.vue";

export default {
    data: () => ({
        supplier_id: "",
        purchase_date: "",
        notes: "",
        payment_status: "",
        file: null,
    }),
    components: {
        InventoryDropdown,
    },
    props: ["suppliers"],
    methods: {
        saveFun() {
            var submitdata = Inertia.form({
                supplier_id: this.supplier_id,
                purchase_date: this.purchase_date,
                notes: this.notes,
                items: this.items,
                payment_status: this.payment_status,
                file: this.file,
            });
            submitdata.post(route("purchases.store"));
            $("#large").modal("toggle");
            this.clearData();
        },
        clearData() {
            this.supplier_id = "";
            this.purchase_date = "";
            this.notes = "";
            this.payment_status = "";
            this.file = null;
            this.$store.commit("clearItemsData");
        },
    },
    computed: {
        items() {
            return this.$store.state.items;
        },
    },
};
</script>
