<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Update Order
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Payment Method</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        required
                                        v-model="payment_channel"
                                    >
                                        <option value="">Select Mode</option>
                                        <option value="1">Cash</option>
                                        <option value="2">Mpesa</option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Order Status</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        required
                                        v-model="status"
                                    >
                                        <option value="">Select Mode</option>
                                        <option value="0">Pending</option>
                                        <option value="1">Prepared</option>
                                        <option value="2">Fullfilled</option>
                                        <option value="99">Cancelled</option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label>Additional Info</label>
                                    <textarea
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="additional_info"
                                    ></textarea>
                                </div>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: "Vertical",
    data: () => ({
        payment_channel: "",
        status: "",
        additional_info: "",
        Id: null,
    }),
    props: ["categories"],

    computed: {
        editdata() {
            return this.$store.state.editData;
        },
    },
    methods: {
        clearData() {
            this.payment_channel = "";
            this.status = "";
            this.additional_info = "";
            this.$store.commit("clearEditData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                payment_channel: this.payment_channel,
                status: this.status,
                additional_info: this.additional_info,
            });

            submitdata.post(route("orders.updateOrder", { order: this.Id }));
            $("#editmodal").modal("toggle");
            this.clearData();
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.payment_channel = value.payment_channel;
                this.status = value.status;
                this.additional_info = value.additional_info;
                this.Id = value.id;
            }
        },
    },
};
</script>
