<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import { Inertia } from "@inertiajs/inertia";
import OrderDetailsModal from "../../components/Orders/OrderDetailsModal.vue";

export default {
    layout: MainLayout,
    components: {
        OrderDetailsModal
    },
    props: ["orders"],
    data: () => ({
        loading: false,
        orderDetails: null,
    }),
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },

    methods: {
        getFun() {
            this.$inertia.get(route("products.index"));
        },

        cancelOrder(order_id, status) {
            var getconfirmation = confirm("Do you wish to cancel this order ?");
            if (getconfirmation == true) {
                this.updateStatus(order_id, status);
            }
        },
        updateStatus(order_id, status) {
            var submitdata = Inertia.form({
                order_id: order_id,
                status: status,
            });

            submitdata.post(route("orders.update"));
        },
        updateMarkPaid(order_id) {
            var getconfirmation = confirm(
                "Do you wish to mark this order as Paid ?"
            );
            if (getconfirmation == true) {
                var submitdata = Inertia.form({
                    order_id: order_id,
                });

                submitdata.post(route("orders.updatePaymentStatus"));
            }
        },
        async showDetails(order_id) {
            $("#editmodal").modal("toggle");
            this.loading=true;
            try {
                var response = await axios({
                    url: route("orders.show", { id: order_id }),
                    method: "GET",
                });

                if (response.data.order !== null) {
                    this.orderDetails = response.data.order;
                }
                this.loading=false;
            } catch (error) {
                this.loading=false;
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },
    },
};
</script>
