<template>
    <div class="col-12">
        <form class="form form-vertical" @submit.prevent>
            <div class="row">
                <div class="col-4">
                    <div class="form-group">
                        <label>Select Supplier</label>
                        <select
                            class="custom-select"
                            id="bankSelect"
                            v-model="submitdata.supplier_id"
                        >
                            <option
                                v-for="(supplier, index) in suppliers"
                                :value="supplier.id"
                                :key="index"
                            >
                                {{ supplier.name }}
                            </option>
                        </select>
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <label>StartDate</label>
                        <input
                            type="date"
                            class="form-control"
                            v-model="submitdata.startdate"
                        />
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <label>EndDate</label>
                        <input
                            type="date"
                            class="form-control"
                            v-model="submitdata.enddate"
                        />
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <label>Select Payment Status</label>
                        <select
                            class="custom-select"
                            v-model="submitdata.payment_status"
                        >
                            <option value="">Select Mode</option>
                            <option value="2">Paid</option>
                            <option value="1">Not Paid</option>
                        </select>
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <label>Select Status</label>
                        <select
                            class="custom-select"
                            v-model="submitdata.status"
                        >
                            <option value="">Select Mode</option>
                            <option value="2">Completed</option>
                            <option value="1">Pending</option>
                        </select>
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <button
                            type="submit"
                            class="btn btn-primary mr-1 mt_17_rem"
                            @click="searchFun(null)"
                        >
                            <feather type="search"></feather>
                        </button>

                        <div class="btn-group dropstart mt_17_rem">
                            <a
                                class="btn btn-outline-success dropdown-toggle btn-sm mr-1"
                                type="button"
                                id="dropdownMenuButton701"
                                data-toggle="dropdown"
                                aria-haspopup="true"
                                aria-expanded="false"
                                href="javascript:void(0);"
                            >
                                <feather type="file-text"></feather>
                            </a>
                            <div
                                class="dropdown-menu"
                                aria-labelledby="dropdownMenuButton701"
                            >
                                <input
                                    class="dropdown-item w-100"
                                    type="submit"
                                    value="Excel Export"
                                    @click="exportFun('excelexport')"
                                />
                                <!-- <input
                                    class="dropdown-item w-100"
                                    type="submit"
                                    value="PDF Export"
                                    @click="exportFun('pdfexport')"
                                /> -->
                            </div>
                        </div>
                    </div>
                </div>

                <div class="col-12" v-if="errors !== null">
                    <span class="badge badge-light-danger mb-2 errorbadge">{{
                        errors
                    }}</span>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    layout: "Vertical",
    data: () => ({
        errors: null,
        submitdata: {
            startdate: null,
            enddate: null,
            supplier_id: null,
            payment_status: null,
            status: null,
            export: null,
        },
    }),
    props: ["suppliers"],
    methods: {
        clearData() {
            this.submitdata.search = null;
        },
        closed() {
            this.submitdata.search = null;
            this.errors = null;
        },
        async searchFun() {
            this.submitdata.export = null;
            try {
                this.$inertia.get(
                        route("reports.purchases"),
                        this.submitdata,
                        {
                            preserveState: true,
                        }
                    );
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        async exportFun(val){
            var that=this;
            this.submitdata.export = val;
            try {
                var response = await axios({
                        url: route("reports.purchases"),
                        method: "GET",
                        responseType: "blob",
                        params: that.submitdata,
                    });
                    this.$store.dispatch("getFormattedTime");
                    var filename =
                        "PurchaseReportExport-" +
                        this.$store.state.timeFileName +
                        ".xlsx";
                    var fileDownload = require("js-file-download");
                    fileDownload(response.data, filename);
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        }
    },
};
</script>
