<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Update Role
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Role Name</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="name"
                                        required
                                    />
                                </div>
                            </div>

                             <div class="col-12" style=" height: 60vh; overflow-y: auto; ">
                                <div class="form-group">
                                    <strong>Permission:</strong>
                                    <br />

                                    <ul class="list-group">
                                        <li
                                            class="list-group-item"
                                            v-for="(item, index) in permissions"
                                            :key="index"
                                        >
                                            <div class="row">
                                                <label class="col-3">{{
                                                    index
                                                }}</label>
                                                <div class="col-9 row">
                                                    <label
                                                        v-for="(
                                                            perm, index2
                                                        ) in item"
                                                        :key="index2"
                                                        class="col-3"
                                                    >
                                                        <input
                                                            type="checkbox"
                                                            name="permission[]"
                                                            class="name"
                                                            v-model="permission"
                                                            :value="perm.id"
                                                        />
                                                        {{
                                                            perm.extracted
                                                        }}</label
                                                    >
                                                </div>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>

                            <div class="col-12 mt-1">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: "Vertical",
    data: () => ({
        name: "",
        Id: null,
        permission:[]
    }),
    props:['permissions'],
    computed: {
        editdata() {
            return this.$store.state.editData;
        },
    },
    methods: {
        clearData() {
            this.name = "";
            //   this.errors = null;
            this.$store.commit("clearEditData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                name: this.name,
                permission:this.permission
            });

            submitdata.patch(
                route("roles.update", { role: this.Id })
            );
            $("#editmodal").modal("toggle");
            this.clearData();
        },

        async retrievePermissions(Id) {
            try {
                var response = await axios({
                    url: route("roles.ajax.permissions", { id: Id }),
                    method: "GET",
                });

                if (response.data.rolePermissions !== null) {
                    this.permission = response.data.rolePermissions;
                }
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.name = value.name;
                this.Id = value.id;

                this.retrievePermissions(value.id)
            }
        },
    },
};
</script>
