require("./bootstrap");

import Vue from "vue";
import { createInertiaApp } from "@inertiajs/inertia-vue";
import VueMeta from "vue-meta";
import { InertiaProgress } from "@inertiajs/progress";
import VueFeather from "vue-feather";
import { method } from "lodash";

InertiaProgress.init({
    color: "red",
    includeCSS: true,
    showSpinner: false,
});
window.store = require("./store").default;

Vue.use(VueMeta);
Vue.use(VueFeather);

Vue.component(
    "SuccessToast",
    require("./Shared/Global/SuccessToast.vue").default
);
Vue.component("ErrorToast", require("./Shared/Global/ErrorToast.vue").default);
Vue.component("Navbar", require("./Shared/Partials/Navbar.vue").default);
Vue.component(
    "LeftsideBar",
    require("./Shared/Partials/LeftsideBar.vue").default
);
Vue.component(
    "Customizer",
    require("./Shared/Partials/Customizer.vue").default
);
Vue.component("Footer", require("./Shared/Partials/Footer.vue").default);
Vue.component("Logo", require("./Shared/Global/Logo.vue").default);
Vue.component(
    "pagination",
    require("./Shared/Global/pagination/Pagination.vue").default
);

createInertiaApp({
    resolve: (name) => require(`./Pages/${name}`),
    setup({ el, App, props, plugin }) {
        Vue.use(plugin);
        Vue.mixin({ methods: { route } });

        new Vue({ store, render: (h) => h(App, props) }).$mount(el);
    },
});
