<template>
  <div
    class="toast toast-wrapper toast-translucent mt-1 customtoast"
    :class="displaystatus == true ? 'show' : ''"
  >
    <div class="toast-header bg-danger">
      <strong class="mr-auto text-white">Error</strong>
      <button type="button" class="ml-1 close" @click="closeToast">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <div class="toast-body bg-light-danger"><b>{{ message }}</b></div>
  </div>
</template>
<style scoped>
.customtoast {
  position: absolute;
  top: 0;
  right: 20px;
}
</style>
<script>
export default {
  created() {},
  computed: {
    displaystatus() {
      return this.$store.state.displayerrorstatus;
    },
    message() {
      var mes = this.$store.state.ErrorMessage;
            var final_mesage="";
            if (typeof mes === "object") {

                for(const key in mes){
                    final_mesage+=mes[key]+"\n";
                }
                // mes.fo((element) => {
                //      final_mesage += element + "<br>";
                // });
                return final_mesage;
            } else {
                return mes;
            }
    },
  },

  methods: {
    closeToast() {
      this.$store.commit("toggleErrorToast");
    },
  },

  watch: {
    displaystatus: function (data) {
      if (data === true) {
        setTimeout(() => {
          this.$store.commit("toggleErrorToast");
        }, 3000);
      }
    },
  },
};
</script>
