<template>
    <div class="content-wrapper ecommerce-application">
        <div class="content-header row">
            <div class="content-header-left col-md-9 col-12 mb-2">
                <div class="row breadcrumbs-top">
                    <div class="col-12">
                        <h2 class="content-header-title float-left mb-0">
                            Products
                        </h2>
                        <div class="breadcrumb-wrapper">
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <!-- <NuxtLink to="/dashboard">Dashboard </NuxtLink> -->
                                </li>
                                <li class="breadcrumb-item active">Products</li>
                            </ol>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="content-body row">
            <section class="col-md-4 col-12">
                <div class="card">
                    <div class="px-2 py-1">
                        <b>Order Items: {{ cart_items.length }}</b>
                    </div>
                    <ul class="dropdown-menu-media p-0 poscart">
                        <li
                            data-v-a58fee00=""
                            class="ps-container scrollable-container media-list scroll-area ps ps--active-y"
                            v-for="(item, index) in cart_items"
                            :key="index"
                        >
                            <div data-v-a58fee00="" class="media">
                                <div
                                    data-v-a58fee00=""
                                    class="media-aside align-self-start"
                                >
                                    <img
                                        data-v-a58fee00=""
                                        :src="item.image"
                                        width="62"
                                        class="rounded"
                                    />
                                </div>
                                <div data-v-a58fee00="" class="media-body">
                                    <div
                                        data-v-a58fee00=""
                                        class="media-heading"
                                    >
                                        <h6
                                            data-v-a58fee00=""
                                            class="cart-item-title pl-2"
                                        >
                                            <a
                                                data-v-a58fee00=""
                                                href="#"
                                                target="_self"
                                                class="text-body"
                                            >
                                                {{ item.name }}
                                            </a>
                                        </h6>
                                    </div>
                                    <div
                                        data-v-a58fee00=""
                                        class="cart-item-qty ml-1"
                                    >
                                        <div
                                            class="input-group bootstrap-touchspin"
                                        >
                                            <span
                                                class="input-group-btn input-group-prepend bootstrap-touchspin-injected"
                                                ><button
                                                    class="btn btn-primary bootstrap-touchspin-down"
                                                    type="button"
                                                    @click="
                                                        updateCartDown(item.id)
                                                    "
                                                >
                                                    <feather
                                                        type="minus"
                                                    ></feather></button></span
                                            ><input
                                                class="touchspin-cart form-control"
                                                type="number"
                                                :value="item.order_qty"
                                            /><span
                                                class="input-group-btn input-group-append bootstrap-touchspin-injected"
                                                ><button
                                                    class="btn btn-primary bootstrap-touchspin-up"
                                                    type="button"
                                                    @click="
                                                        updateCartUp(item.id)
                                                    "
                                                >
                                                    <feather
                                                        type="plus"
                                                    ></feather></button
                                            ></span>
                                        </div>
                                    </div>
                                    <h5
                                        data-v-a58fee00=""
                                        class="cart-item-price"
                                    >
                                        Ksh.{{ item.price * item.order_qty }}
                                    </h5>
                                </div>
                            </div>
                        </li>
                    </ul>
                    <div>
                        <form
                            action=""
                            v-if="cart_items.length > 0"
                            @submit.prevent="createOrder()"
                        >
                            <div class="col-12">
                                <div class="form-group">
                                    <label> Client Name</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        required
                                        v-model="submitdata.client_name"
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Additional Info</label>
                                    <textarea
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="submitdata.additional_info"
                                    ></textarea>
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Payment Mode</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        required
                                        v-model="submitdata.payment_channel"
                                    >
                                        <option value="">Select Mode</option>
                                        <option value="1">Cash</option>
                                        <option value="2">Mpesa</option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <button class="btn btn-primary">
                                        Make Order
                                    </button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </section>
            <div class="col-md-8 col-12">
                <section id="ecommerce-searchbar" class="ecommerce-searchbar">
                    <POSSearch></POSSearch>
                </section>
                <!-- E-commerce Search Bar Ends -->

                <!-- E-commerce Products Starts -->
                <section id="ecommerce-products" class="grid-view">
                    <div
                        class="card ecommerce-card"
                        v-for="(item, index) in menues.data"
                        :key="index"
                    >
                        <div class="item-img text-center">
                            <img
                                class="img-fluid card-img-top imagepos"
                                :src="item.image"
                                alt="img-placeholder"
                            />
                        </div>
                        <div class="card-body">
                            <div class="item-wrapper">
                                <div>
                                    <h6 class="item-price">
                                        Ksh.{{ item.price }}
                                    </h6>
                                </div>
                            </div>
                            <h6 class="item-name">
                                {{ item.name }}
                            </h6>
                        </div>
                        <div class="item-options text-center">
                            <button
                                class="btn btn-primary btn-cart"
                                @click="addToCart(item)"
                            >
                                <feather type="shopping-cart"></feather>
                                <span class="add-to-cart">Add to cart</span>
                            </button>
                        </div>
                    </div>
                </section>

                <div class="row">
                    <div class="col-sm-12">
                        <pagination :data="menues" :limit="1">
                            <span slot="prev-nav"
                                ><feather type="arrow-left"></feather
                            ></span>
                            <span slot="next-nav"
                                ><feather type="arrow-right"></feather
                            ></span>
                        </pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import POSSearch from "../../components/POS/POSSearch.vue";
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: MainLayout,
    components: {
        POSSearch,
    },

    props: ["categories", "menues"],
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },
    data: () => ({
        cart: null,
        cart_items: [],
        submitdata: {
            client_name: "",
            additional_info: "",
            payment_channel: 1,
            cart_id: "",
        },
    }),
    methods: {
        getFun() {
            this.$inertia.get(route("products.index"));
        },
        editFun(data) {
            this.$store.commit("editDataState", data);
        },
        toggleSearchForm() {
            this.$store.commit("toggleSearch");
        },
        deleteFun(id) {
            var getconfirmation = confirm(
                "Are you sure You Want to Delete This Record?"
            );
            if (getconfirmation == true) {
                this.$inertia.delete(route("products.destroy", id));
            }
        },

        async exportFun() {
            var response = await axios({
                url: route("products.export"),
                method: "GET",
                responseType: "blob",
            });
            this.$store.dispatch("getFormattedTime");
            var filename =
                "CategoryExport-" + this.$store.state.timeFileName + ".xlsx";
            var fileDownload = require("js-file-download");
            fileDownload(response.data, filename);
            try {
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        async addToCart(item) {
            var findata = {
                cart_id: this.cart !== null ? this.cart : null,
                menu_id: item.id,
            };

            try {
                var response = await axios({
                    url: route("pos.addToCart"),
                    method: "POST",
                    data: findata,
                });
                this.$store.commit("toggleSuccessToast", response.data.message);

                this.retrieveCart();
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        async updateCartUp(cartitemid) {
            try {
                var response = await axios({
                    url: route("pos.updateCartUp"),
                    method: "POST",
                    data: { cart_item_id: cartitemid },
                });
                this.$store.commit("toggleSuccessToast", response.data.message);
                this.retrieveCart();
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        async updateCartDown(cartitemid) {
            try {
                var response = await axios({
                    url: route("pos.updateCartDown"),
                    method: "POST",
                    data: { cart_item_id: cartitemid },
                });
                this.$store.commit("toggleSuccessToast", response.data.message);
                this.retrieveCart();
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        async retrieveCart() {
            try {
                var response = await axios({
                    url: route("pos.cart"),
                    method: "GET",
                });

                if (response.data.cart !== null) {
                    this.cart = response.data.cart.id;
                    this.cart_items = response.data.cart_items;
                }
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        createOrder() {
            var that = this;
            that.submitdata.cart_id = that.cart;
            var submitdata = Inertia.form(that.submitdata);
            submitdata.post(route("pos.makeOrder"));
            $("#large").modal("toggle");
            this.clearData();
        },

        clearData() {
            this.submitdata.client_name = "";
            this.submitdata.additional_info = "";
            this.submitdata.cart_id = "";
            this.submitdata.payment_channel = 1;
            this.cart = null;
            this.cart_items = [];
        },
    },

    mounted() {
        this.retrieveCart();
    },
};
</script>
