<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Update Expense Category
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Category Name</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="name"
                                        required
                                    />
                                </div>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: "Vertical",
    data: () => ({
        name: "",
        Id: null,
    }),
    computed: {
        editdata() {
            return this.$store.state.editData;
        },
    },
    methods: {
        clearData() {
            this.name = "";
            //   this.errors = null;
            this.$store.commit("clearEditData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                name: this.name,
            });

            submitdata.patch(
                route("expense_categories.update", { expense_category: this.Id })
            );
            $("#editmodal").modal("toggle");
            this.clearData();
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.name = value.name;
                this.Id = value.id;
            }
        },
    },
};
</script>
