<template>
    <div
        class="modal fade text-left"
        id="viewmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Purchase Items
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="table-responsive table-hover table-striped">
                        <table class="table">
                            <thead>
                                <tr>
                                    <th>#</th>
                                    <th>Name</th>
                                    <th>Quantity</th>
                                    <th>Price</th>
                                    <th>Units</th>
                                    <th>Sub Total</th>
                                </tr>
                            </thead>

                            <tbody>
                                <tr
                                    v-for="(item, index) in purchase_items"
                                    :key="index"
                                >
                                    <td>{{ index + 1 }}</td>
                                    <td>#{{ item.name }}</td>
                                    <td>{{ item.quantity }}</td>
                                    <td>Ksh.{{ item.price }}</td>
                                    <td>{{ item.units }}</td>
                                    <td>
                                        Ksh.{{ item.price * item.quantity }}
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data: () => ({
        errors: null,
        purchase_items: [],
    }),
    methods: {
        async retrieveItems(Id) {
            try {
                var response = await axios({
                    url: route("purchases.show", { id: Id }),
                    method: "GET",
                });

                if (response.data.items !== null) {
                    this.purchase_items = response.data.items;
                }
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },

        clearData() {
            this.purchase_items = [];
        },
    },
    computed: {
        editdata() {
            return this.$store.state.editData;
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.retrieveItems(value.id);
            }
        },
    },
};
</script>
