<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Update Expense
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Category</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        id="bankSelect"
                                        v-model="category_id"
                                    >
                                        <option value="">
                                            Select Category
                                        </option>
                                        <option
                                            v-for="(
                                                category, index
                                            ) in categories"
                                            :value="category.id"
                                            :key="index"
                                        >
                                            {{ category.name }}
                                        </option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label> Expense For</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="expensefor"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Notes</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="notes"
                                        v-model="notes"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Amount</label>
                                    <input
                                        type="number"
                                        class="form-control"
                                        v-model="amount"
                                        required
                                    />
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group" v-if="status == false">
                                    <label>Attach File</label>
                                    <input
                                        type="file"
                                        class="form-control"
                                        @input="file = $event.target.files[0]"
                                        required
                                    />
                                </div>
                                <div
                                    v-else
                                    class="card p-2"
                                    style="width: min-content"
                                >
                                    <a
                                        :href="placeholder"
                                        class="rounded-3 shadow-4"
                                        style="width: 100px"
                                        >{{ placeholder }}</a
                                    >
                                    <a
                                        @click="remove"
                                        href="#"
                                        class="btn btn-sm text-danger"
                                        ><feather type="trash-2"></feather
                                    ></a>
                                </div>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: "Vertical",
    data: () => ({
        category_id: "",
        expensefor: "",
        expense_date: "",
        notes: "",
        amount: "",
        Id: null,
        file: null,
        status: false,
        placeholder: "",
    }),
    props: ["categories"],

    computed: {
        editdata() {
            return this.$store.state.editData;
        },
    },
    methods: {
        clearData() {
            this.category_id = "";
            this.expensefor = "";
            this.notes = "";
            this.amount = "";
            this.expense_date = "";
            this.placeholder = "";
            this.file = null;
            this.status = false;
            this.$store.commit("clearEditData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                category_id: this.category_id,
                expensefor: this.expensefor,
                expense_date: this.expense_date,
                notes: this.notes,
                amount: this.amount,
                file: this.file,
                _method: "patch",
            });

            submitdata.post(route("expenses.update", { expense: this.Id }));
            $("#editmodal").modal("toggle");
            this.clearData();
        },
        remove() {
            this.status = false;
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.category_id = value.category_id;
                this.expensefor = value.expensefor;
                this.expense_date = value.expense_date;
                this.notes = value.notes;
                this.amount = value.amount;
                this.status = value.filestatus;
                this.placeholder = value.file;
                this.Id = value.id;
            }
        },
    },
};
</script>
