<template>
    <div>
        <b>Items Selected : ({{ inputs.length }})</b>
        <hr />

        <div class="maxheight400">
            <div class="row" v-for="(item, index) in inputs" :key="index">
                <input
                    type="hidden"
                    name="inventory_id[]"
                    class="form-control"
                    v-model="item.product_id"
                />

                <div class="form-group col-md-5">
                    <div class="col-sm-12">
                        <label v-if="index == 0">Name</label>
                        <input
                            type="text"
                            name="inventory_name[]"
                            class="form-control py-2"
                            v-model="item.name"
                            readonly
                        />
                    </div>
                </div>

                <div class="form-group col-md-5">
                    <div>
                        <label v-if="index == 0">Quantity</label>
                        <input
                            type="text"
                            name="inventory_quantity[]"
                            class="form-control py-2"
                            v-model="item.quantity"
                            readonly
                        />
                    </div>
                </div>

                <div class="col-md-1 pt-1">
                    <span
                        @click="deleteRow(index)"
                        class="btn btn-danger btn-sm text-white"
                        :class="index == 0 ? 'mt-2' : ''"
                        ><feather type="trash"></feather
                    ></span>
                </div>
            </div>
        </div>
        <hr />
        <div class="row">
            <div class="form-group col-md-5">
                <div class="col-sm-12">
                    <label>Select Item</label>
                    <select
                        class="table-group-action-input form-control input-medium test"
                        :id="dynamic_id"
                    ></select>
                </div>
            </div>

            <input
                type="hidden"
                :id="'inventory_name' + this.dynamic_id"
                class="form-control py-2"
                readonly
            />

            <input
                type="hidden"
                :id="'inventory_units' + this.dynamic_id"
                class="form-control py-2"
                readonly
            />

            <div class="col-md-5">
                <label>Quantity</label>
                <input
                    type="number"
                    :id="'inventory_quantity' + this.dynamic_id"
                    class="form-control py-2"
                />
            </div>

            <div class="col-md-1 pt-1">
                <span
                    class="btn btn-primary btn-sm text-white mt-2"
                    @click="addRow"
                    ><feather type="plus"></feather
                ></span>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data() {
        return {
            inputs: [],
        };
    },
    props: ["dynamic_id"],
    methods: {
        addRow() {
            var id = $("#" + this.dynamic_id);
            var name = $("#inventory_name" + this.dynamic_id);
            var quantity = $("#inventory_quantity" + this.dynamic_id);
            var units = $("#inventory_units" + this.dynamic_id);

            var check = this.inputs.some(
                (item) => parseInt(item.product_id) == parseInt(id.val())
            );

            if (id.val() == "" || quantity.val() == "") {
                this.$store.commit("toggleErrorToast", "Please Select An Item");
            } else if (check) {
                id.val("").change();
                name.val("");
                quantity.val("");
                units.val("");

                this.$store.commit(
                    "toggleErrorToast",
                    "Please Select A Different Item"
                );
            } else {
                this.inputs.push({
                    product_id: id.val(),
                    name: name.val(),
                    quantity: quantity.val(),
                    units: units.val(),
                });

                this.$store.state.items = this.inputs;

                id.val("").change();
                name.val("");
                quantity.val("");
                units.val("");
            }
        },
        deleteRow(index) {
            this.inputs.splice(index, 1);
            this.$store.state.inputs = this.inputs;
        },
        queryInventory(value) {
            if (value !== null) {
                axios
                    .get(route("purchases.inventory.specific"), {
                        params: { id: value },
                    })
                    .then((resp) => {
                        var data = resp.data.inventory;
                        $("#inventory_name" + this.dynamic_id).val(data.name);
                        $("#inventory_units" + this.dynamic_id).val(data.units);
                    })
                    .catch((error) => {
                        this.error = error.response.data.message;
                    });
            }
        },
        async updateItemsInStore(Id) {
            var response = await axios({
                url: route("production.retrieveItems"),
                method: "POST",
                data: { id: Id },
            });

            this.inputs = [];
            response.data.items.forEach((element) => {
                this.inputs.push({
                    product_id: element.product_id,
                    name: element.name,
                    quantity: element.quantity,
                    units: element.units,
                });
            });

            this.$store.state.items = this.inputs;
        },
    },
    mounted() {
        var that = this;

        $("#" + this.dynamic_id).on("change", function () {
            $("#inventory_name" + this.dynamic_id).val("");
            $("#inventory_quantity" + this.dynamic_id).val("");
            $("#inventory_units" + this.dynamic_id).val("");
            that.queryInventory($(this).val());
        });

        $(".test").select2({
            placeholder: "inventory...",
            allowClear: true,
            ajax: {
                url: route("purchases.inventory"),
                dataType: "json",
                delay: 300,
                processResults: function (data) {
                    return {
                        results: $.map(data.results, function (item) {
                            return {
                                text: item.name,
                                id: item.id,
                            };
                        }),
                        pagination: {
                            more: data.pagination.more,
                        },
                    };
                },
                data: function (params) {
                    return {
                        term: params.term || "",
                        page: params.page || 1,
                    };
                },

                cache: true,
            },
        });
    },

    computed: {
        editdata() {
            return this.$store.state.editData;
        },
        items() {
            return this.$store.state.items;
        },
    },

    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.updateItemsInStore(value.id);
            }
        },
        items: function (value) {
            if (value.length == 0) {
                this.inputs = [];
            }
        },
    },
};
</script>
