<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">Update User</h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>User Name</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="name"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Email Address</label>
                                    <input
                                        type="email"
                                        class="form-control"
                                        placeholder="Email"
                                        v-model="email"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Password</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Password"
                                        v-model="password"
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Confirm Password</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Password"
                                        v-model="confirm_password"
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Role</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        id="bankSelect"
                                        multiple
                                        v-model="role_id"
                                    >
                                        <option
                                            v-for="(role, index) in roles"
                                            :value="role.name"
                                            :key="index"
                                        >
                                            {{ role.name }}
                                        </option>
                                    </select>
                                </div>
                            </div>
                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: "Vertical",
    data: () => ({
        name: "",
        email: "",
        password: "",
        confirm_password: "",
        Id: null,
        role_id: [],
    }),
    props: ["roles"],
    computed: {
        bankeditdata() {
            return this.$store.state.editData;
        },
    },
    methods: {
        clearData() {
            this.name = "";
            this.email = "";
            this.password = "";
            this.confirm_password = "";
            this.role_id = [];
            //   this.errors = null;
            this.$store.commit("clearEditData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                name: this.name,
                email: this.email,
                password: this.password,
                confirm_password: this.confirm_password,
                role_id: this.role_id,
            });

            submitdata.patch(route("users.update", { user: this.Id }));
            $("#editmodal").modal("toggle");
            this.clearData();
        },
    },
    watch: {
        bankeditdata: function (value) {
            if (value !== null) {
                this.name = value.name;
                this.email = value.email;
                this.Id = value.id;
                this.role_id = _.map(value.roles, "name");

            }
        },
    },
};
</script>
