<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">Update Product</h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Select Category</label>
                                    <select
                                        class="select2 form-control form-control-lg"
                                        id="bankSelect"
                                        v-model="category_id"

                                    >
                                        <option value="">Select Category</option>
                                        <option v-for="(category, index) in categories" :value="category.id" :key="index">{{ category.name }}</option>
                                    </select>
                                </div>
                            </div>

                            <div class="col-12">
                                <div class="form-group">
                                    <label>Product Name</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="name"
                                        required
                                    />
                                </div>
                            </div>
                             <div class="col-12">
                                <div class="form-group">
                                    <label>Product Price</label>
                                    <input
                                        type="number"
                                        class="form-control"
                                        v-model="price"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Product Units</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Units"
                                        v-model="units"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Description</label>
                                    <input
                                        type="text"
                                        class="form-control"
                                        placeholder="Name"
                                        v-model="description"

                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Quantity</label>
                                    <input
                                        type="number"
                                        class="form-control"
                                        v-model="stock_qty"
                                        required
                                    />
                                </div>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: "Vertical",
    data: () => ({
        category_id: "",
        name: "",
        description: "",
        stock_qty: "",
        price: "",
        units: "",
        Id: null,
    }),
    props:["categories"],

    computed: {
        editdata() {
            return this.$store.state.editData;
        },
    },
    methods: {
        clearData() {
           this.category_id = "";
            this.name = "";
            this.description = "";
            this.stock_qty = "";
            this.price = "";
            this.units = "";
            //   this.errors = null;
            this.$store.commit("clearEditData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                category_id: this.category_id,
                name: this.name,
                description: this.description,
                stock_qty: this.stock_qty,
                price: this.price,
                units: this.units,
            });

            submitdata.patch(route("products.update", { product: this.Id }));
            $("#editmodal").modal("toggle");
            this.clearData();
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.category_id = value.category_id;
                this.name = value.name;
                this.description = value.description;
                this.stock_qty = value.stock_qty;
                this.price = value.price;
                this.units = value.units;
                this.Id = value.id;
            }
        },
    },
};
</script>
