                        </div>

                        <div v-show="displaySearch">
                            <OrderSearch></OrderSearch>
                        </div>

                        <div class="table-responsive table-hover table-striped">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>OrderNo</th>
                                        <th>Amount</th>
