                                    >
                                        <td>{{ index + 1 }}</td>
                                        <td>#{{ item.order_no }}</td>
                                        <td>Ksh.{{ item.order_amount }}</td>
                                        <td>{{ item.client_name }}</td>
                                        <td>
                                            {{ item.additional_info || "N/A" }}
                                        </td>
                                        <td>
                                            <span :class="item.class">{{
                                                item.stringstatus
                                            }}</span>
                                        </td>
                                        <td>{{ item.mode }}</td>
                                        <td>
                                            <span :class="item.p_class">{{
                                                item.p_status
                                            }}</span>
