<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import OrderSearch from "../../components/Orders/OrderSearch.vue";
import OrderEditModal from "../../components/Orders/OrderEditModal.vue";
export default {
    layout: MainLayout,
    components: {
        OrderSearch,
        OrderEditModal,
    },
    props: ["orders"],
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },

    methods: {
        getFun() {
            this.$inertia.get(route("orders.retrieve"));
        },
        editFun(data) {
            this.$store.commit("editDataState", data);
        },
        toggleSearchForm() {
            this.$store.commit("toggleSearch");
        },
        deleteFun(id) {
            var getconfirmation = confirm(
                "Are you sure You Want to Delete This Record?"
            );
            if (getconfirmation == true) {
                this.$inertia.delete(route("orders.destroy", id));
            }
        },
    },
};
</script>
