<template>
    <div class="content-wrapper">
        <div class="content-header row">
            <div class="content-header-left col-md-9 col-12 mb-2">
                <div class="row breadcrumbs-top">
                    <div class="col-12">
                        <h2 class="content-header-title float-left mb-0">
                            Purchase Report
                        </h2>
                        <div class="breadcrumb-wrapper">
                            <ol class="breadcrumb">
                                <li class="breadcrumb-item">
                                    <!-- <NuxtLink to="/dashboard">Dashboard </NuxtLink> -->
                                </li>
                                <li class="breadcrumb-item active">
                                    Purchase Report
                                </li>
                            </ol>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="content-body">
            <div class="row" id="basic-table">
                <div class="col-12">
                    <div class="card">
                        <div class="card-header py-1">
                            <h4 class="card-title">Purchase Report</h4>

                            <div class="float-right">
                                
                                <button
                                    class="btn btn-outline-primary float-right mr-1 btn-sm"
                                    @click="getFun"
                                >
                                    <feather type="refresh-ccw"></feather>
                                </button>
                                 <button
                                    class="btn btn-outline-primary float-right mr-1 btn-sm"
                                    @click="toggleSearchForm"
                                >
                                    <feather type="search"></feather>
                                </button>
                            </div>
                        </div>

                         <div v-show="displaySearch">
                            <PurchaseSearch :suppliers="suppliers"></PurchaseSearch>
                        </div>

                        <div class="table-responsive table-hover table-striped">
                            <table class="table">
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>PurchaseNo</th>
                                        <th>Supplier</th>
                                        <th style="min-width:150px;">Purchase Date</th>
                                        <th>Notes</th>
                                        <th>Amount</th>
                                        <th>Payment Status</th>
                                        <th>Status</th>
                                        <th>CreatedBy</th>
                                    </tr>
                                </thead>

                                <tbody>
                                    <tr
                                        v-for="(item, index) in purchases.data"
                                        :key="index"
                                    >
                                        <td>{{ index + 1 }}</td>
                                        <td>#{{ item.purchase_no }}</td>
                                        <td>Ksh.{{ item.supplier.name }}</td>
                                        <td>{{ new Date(item.purchase_date).toLocaleDateString() }}</td>
                                        <td>
                                            {{ item.notes || "N/A" }}
                                        </td>
                                        <td>Ksh.{{ item.amount }}</td>
                                        <td>
                                            <span :class="item.p_class">{{
                                                item.p_status
                                            }}</span>
                                        </td>
                                         <td>
                                            <span :class="item.b_class">{{
                                                item.b_status
                                            }}</span>
                                        </td>
                                        <td>{{ item.user.name }}</td>
                                    </tr>
                                </tbody>
                            </table>
                            <div class="mx-2 mt-3">
                                <pagination :data="purchases" :limit="1">
                                    <span slot="prev-nav"
                                        ><feather type="arrow-left"></feather
                                    ></span>
                                    <span slot="next-nav"
                                        ><feather type="arrow-right"></feather
                                    ></span>
                                </pagination>

                                <!-- <pagination class="mt-6" :links="users.links" /> -->
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>

<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import PurchaseSearch from "../../components/Reports/PurchaseSearch.vue";
import { Inertia } from "@inertiajs/inertia";

export default {
    layout: MainLayout,
    components: {
        PurchaseSearch
    },
    props: ["purchases", "suppliers"],
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },

    methods: {
        getFun() {
            this.$inertia.get(route("reports.purchases"));
        },
        editFun(data) {
            this.$store.commit("editDataState", data);
        },
        toggleSearchForm() {
            this.$store.commit("toggleSearch");
        },
        deleteFun(id) {
            var getconfirmation = confirm(
                "Are you sure You Want to Delete This Record?"
            );
            if (getconfirmation == true) {
                this.$inertia.delete(route("purchases.destroy", id));
            }
        },
        updateStatus(id) {
            var submitdata = Inertia.form({
                id: id
            });
            var getconfirmation = confirm(
                "Are you sure You Want to Update Purchase Status to complete?"
            );
            if (getconfirmation == true) {
                submitdata.post(route("purchases.updateStatus"));
            }
        },
    },
};
</script>
