<template>
    <div
        class="modal fade text-left"
        id="importexcel"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Upload Users
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="uploadFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label>Import</label>
                                    <input
                                        type="file"
                                        class="form-control"
                                        placeholder="Bank Code"
                                        id="file"
                                        ref="file"
                                        v-on:change="handleFileUpload($event)"
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Upload
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";

export default {
    data: () => ({
        file: "",
    }),
    methods: {
        async uploadFun() {
            var submitdata = Inertia.form({
                upload: this.file,
            });
            submitdata.post(route("users.import"));
            $("#importexcel").modal("toggle");
        },
        clearData() {
            this.file = "";
            $("#file").val("");
        },
        handleFileUpload(event) {
            this.file = event.target.files[0];
        },
    },
};
</script>
