                                        <td>{{ item.expensefor }}</td>
                                        <td>{{ new Date(item.expense_date).toLocaleDateString() }}</td>
                                        <td>{{ item.notes }}</td>
                                        <td>
                                            Ksh.{{
                                                item.amount.toLocaleString()
                                            }}
