<script>
import MainLayout from "../../Shared/Layouts/Vertical.vue";
import ExpenseSearch from "../../components/Expenses/ExpenseSearch.vue";
import ExpenseCreate from "../../components/Expenses/ExpenseCreateModal.vue";
import ExpenseEdit from "../../components/Expenses/ExpenseEditModal.vue";
export default {
    layout: MainLayout,
    components: {
        ExpenseSearch,
        ExpenseCreate,
        ExpenseEdit,
    },
    props: ["categories", "expenses"],
    computed: {
        displaySearch() {
            return this.$store.state.displaysearch;
        },
        searchState() {
            return this.$store.state.search;
        },
    },

    methods: {
        getFun() {
            this.$inertia.get(route("expenses.index"));
        },
        editFun(data) {
            this.$store.commit("editDataState", data);
        },
        toggleSearchForm() {
            this.$store.commit("toggleSearch");
        },
        deleteFun(id) {
            var getconfirmation = confirm(
                "Are you sure You Want to Delete This Record?"
            );
            if (getconfirmation == true) {
                this.$inertia.delete(route("expenses.destroy", id));
            }
        },
    },
};
</script>
