<template>
    <div class="col-12 mt-0">
        <div class="row">
            <div class="input-group input-group-merge">
                <input
                    type="text"
                    class="form-control search-product"
                    id="shop-search"
                    placeholder="Search Menu"
                    aria-label="Search..."
                    aria-describedby="shop-search"
                    @keyup="searchFun"
                    v-model="submitdata.search"
                />
                <div class="input-group-append">
                    <span class="input-group-text">
                        <feather type="search"></feather>
                    </span>
                </div>
            </div>

            <div class="col-12" v-if="errors !== null">
                <span class="badge badge-light-danger mb-2 errorbadge">{{
                    errors
                }}</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    layout: "Vertical",
    data: () => ({
        errors: null,
        submitdata: {
            search: null,
        },
        timer: null,
    }),
    props: ["categories"],
    methods: {
        clearData() {
            this.submitdata.search = null;
        },
        closed() {
            this.submitdata.search = null;
            this.errors = null;
        },
        async searchFun() {

            if (this.timer) {
                clearTimeout(this.timer);
                this.timer = null;
            }
            
            this.timer = setTimeout(() => {
                try {
                    this.$inertia.get(route("pos.index"), this.submitdata, {
                        preserveState: true,
                    });
                } catch (error) {
                    if (error.response) {
                        this.errors = error.response.data.message;
                    } else {
                        this.errors = "Something Went Wrong";
                    }
                }
            }, 800);
        },
    },
};
</script>
