<template>
    <div class="col-12">
        <form class="form form-vertical" @submit.prevent="searchFun">
            <div class="row">
                <div class="col-4">
                    <div class="form-group">
                        <label>Select Category</label>
                        <select
                            class="custom-select"
                            id="bankSelect"
                            v-model="submitdata.category_id"
                        >
                            <option
                                v-for="(category, index) in categories"
                                :value="category.id"
                                :key="index"
                            >
                                {{ category.name }}
                            </option>
                        </select>
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <label>Search</label>

                        <input
                            type="text"
                            class="form-control"
                            placeholder="Search by Name or notes"
                            v-model="submitdata.search"
                        />
                    </div>
                </div>
                <div class="col-4">
                    <div class="form-group">
                        <button
                            type="submit"
                            class="btn btn-primary mr-1 mt_17_rem"
                        >
                            Search
                        </button>
                    </div>
                </div>

                <div class="col-12" v-if="errors !== null">
                    <span class="badge badge-light-danger mb-2 errorbadge">{{
                        errors
                    }}</span>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
export default {
    layout: "Vertical",
    data: () => ({
        errors: null,
        submitdata: {
            search: null,
            category_id: null,
        },
    }),
    props: ["categories"],
    methods: {
        clearData() {
            this.submitdata.search = null;
        },
        closed() {
            this.submitdata.search = null;
            this.errors = null;
        },
        async searchFun() {
            try {
                this.$inertia.get(route("expenses.index"), this.submitdata, {
                    preserveState: true,
                });
            } catch (error) {
                if (error.response) {
                    this.errors = error.response.data.message;
                } else {
                    this.errors = "Something Went Wrong";
                }
            }
        },
    },
};
</script>
