<template>
    <div
        class="modal fade text-left"
        id="editmodal"
        tabindex="-1"
        role="dialog"
        aria-labelledby="myModalLabel17"
        aria-hidden="true"
    >
        <div
            class="modal-dialog modal-dialog-centered modal-lg"
            role="document"
        >
            <div class="modal-content">
                <div class="modal-header">
                    <h4 class="modal-title" id="myModalLabel17">
                        Edit Production
                    </h4>
                    <button
                        type="button"
                        class="close"
                        data-dismiss="modal"
                        aria-label="Close"
                        @click="clearData"
                    >
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <form
                        class="form form-vertical"
                        @submit.prevent="updateFun()"
                    >
                        <div class="row">
                            <div class="col-12">
                                <div class="form-group">
                                    <label> Production Date</label>
                                    <input
                                        type="date"
                                        class="form-control"
                                        v-model="production_date"
                                        required
                                    />
                                </div>
                            </div>
                            <div class="col-12">
                                <InventoryDropdown
                                    dynamic_id="select2_edit"
                                ></InventoryDropdown>
                            </div>

                            <div class="col-12">
                                <button
                                    type="submit"
                                    class="btn btn-primary mr-1"
                                >
                                    Save
                                </button>
                            </div>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { Inertia } from "@inertiajs/inertia";
import InventoryDropdown from "../../components/Production/InventoryDropdown.vue";

export default {
    layout: "Vertical",
    data: () => ({
        production_date: "",
        Id: null,
    }),
    components: {
        InventoryDropdown,
    },

    computed: {
        editdata() {
            return this.$store.state.editData;
        },
        items() {
            return this.$store.state.items;
        },
    },
    methods: {
        clearData() {
            this.production_date = "";
            this.$store.commit("clearItemsData");
        },

        async updateFun() {
            var submitdata = Inertia.form({
                production_date: this.production_date,
                items: this.items,
                _method: "patch",
            });

            submitdata.post(
                route("production.update", { production: this.Id })
            );
            $("#editmodal").modal("toggle");
            this.clearData();
        },
        remove() {
            // this.status = false;
        },
    },
    watch: {
        editdata: function (value) {
            if (value !== null) {
                this.production_date = value.production_date;
                this.Id = value.id;
            }
        },
    },
};
</script>
