<template>
  <div
    class="toast toast-wrapper toast-translucent mt-1 customtoast"
    :class="displaystatus == true ? 'show' : ''"
  >
    <div class="toast-header bg-success">
      <strong class="mr-auto text-white">Success</strong>
      <button type="button" class="ml-1 close" @click="closeToast">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
    <div class="toast-body bg-light-success"><b>{{ message }}</b></div>
  </div>
</template>
<style scoped>
.customtoast {
  position: absolute;
  top: 0;
  right: 20px;
}
</style>
<script>
export default {
  created() {},
  computed: {
    displaystatus() {
      return this.$store.state.displaystatus;
    },
    message() {
      return this.$store.state.SuccessMessage;
    },
  },

  methods: {
    closeToast() {
      this.$store.commit("toggleSuccessToast");
    },
  },

  watch: {
    displaystatus: function (data) {
      if (data === true) {
        setTimeout(() => {
          this.$store.commit("toggleSuccessToast");
        }, 2000);
      }
    },
  },
};
</script>
